#btn{
  background-image: url("comp/playNow/button.png");
  width: 230px;
  height: 50px;
  margin-left: 250px;
  border: 1px solid black;
  border-radius: 10px;
}
#btn:hover{
  background-image: url("comp/playNow/button-hover.png");
  width: 220px;
  height: 47px;
  border: 1px solid black;
  border-radius: 10px;
}
.score{
  padding-top: 50px;
  padding-left: 60px;
}
.num{
  margin-right: 20px;
  height: 60px;
  width: 60px;
  border-radius: 0px;
  border: 1px solid black;
}
.num:nth-child(1){
  background-image: url("comp/defaultnum/1.png");
  background-position: center;
}
.num:nth-child(2){
  background-image: url("comp/defaultnum/2.png");
  background-position: center;
}
.num:nth-child(3){
  background-image: url("comp/defaultnum/3.png");
  background-position: center;
}
.num:nth-child(4){
  background-image: url("comp/defaultnum/4.png");
  background-position: center;
}
.num:nth-child(5){
  background-image: url("comp/defaultnum/5.png");
  background-position: center;
}
.num:nth-child(6){
  background-image: url("comp/defaultnum/6.png");
  background-position: center;
}
#popins{
  font-family: 'Poppins';
}
.cube{
  width:150px;
  margin-left: 44%;
  transition: transform 2s ;
  transform-origin: 50% 50%;
}
.dice h1{
  margin-left:40%;
}
.btns button{
  width:180px;
  height: 40px;
}
.btns button:nth-child(1){
  background-image: url("comp/belowdice/reset.png");
  background-position: center;
  border:1px solid black;
  border-radius: 5px;
}
.btns button:nth-last-child(1){
  background-image: url("comp/belowdice/showRules.png");
  background-position: center;
  border:1px solid black;
  border-radius: 5px;
}